import {createStore} from "redux";

const CHANGING = "CHANGING";
const changeText = (e) => ({
  type: CHANGING,
  value: e.target.value
})
const initialMarkDown =(
`
# H1 header
## H2 header
[link to freeCodeCamp](https://forum.freecodecamp.org)
\`<h1>This is here some inline code<h1>\`
\`\`\`
<h1>Hello world!</h1>
<p>this is a paragraph</p>
\`\`\`
* List 1
  * List child

> This is a blockquote

![](https://marked.js.org/img/logo-black.svg)
**I am bold**
`);
const initialState = {
  input: initialMarkDown
}
const reducer = (state=initialState, action) => {
  switch(action.type) {
    case(CHANGING)
    : return Object.assign({}, state, {input: action.value})
    default
    : return state
  }
}
export const store = createStore(reducer);

export const mapS2P = (state) => ({
  writtenText : state.input
})
export const mapD2P = (dispatch) => ({
  changeText: (e) => dispatch(changeText(e))
})