export const Presentational = (props) => {
  return (
    <EditorWrapper propys={props}/>
  );
}

const EditorWrapper = (props) => {
  const propys = props.propys;
  const img = <img style={{maxWidth:"3em"}} src="https://marked.js.org/img/logo-black.svg"/>;
  const headerStyle={
    display:"flex",
    alignItems:"center", 
    margin:"1rem auto", 
    maxWidth:"max-content"
  };
  return (
    <div>
      <h1 style={headerStyle}>Markdown Previewer{img}</h1>
      <Editor propys = {propys}/>
      <Preview 
        writtenText={propys.writtenText}
        Marked = {propys.Marked}
        ReactHtmlParser = {propys.ReactHtmlParser}
      />
    </div>
  );
}

const Editor = (props) => {
  const propys = props.propys;
  return (
    <div>
      <textarea 
      id="editor"
      value={propys.writtenText}
      onChange={propys.changeText.bind(this)}
      />
    </div>
  );
}
const Preview = (props) => {
  const Marked = props.Marked;
  const ReactHtmlParser = props.ReactHtmlParser;
  const markDown = ReactHtmlParser(Marked(props.writtenText, {breaks: true}));
  return (
    <div id="preview">
      {markDown}
    </div>
  );
}